import React, { Component } from 'react';
import fire from '../config/Fire';
import 'bootstrap/dist/css/bootstrap.min.css';
import Item from './Item';
import SimpleLineIcon from 'react-simple-line-icons';

class Home extends Component {
    constructor(props) {
        super(props);
        
        this.handleChange = this.handleChange.bind(this);
        this.publicar = this.publicar.bind(this);
        this.buscar = this.buscar.bind(this);
        this.logout = this.logout.bind(this);
        this.state = {postagens : [],
                      novapostagem: ''};
    }

    buscar(){
        let dados = [];

        var firebaseRef = fire.database().ref('/postagens').orderByChild("texto");
        firebaseRef.once('value')
          .then((snapshot) => {

            let dados=[];

            
            if (snapshot.val() == null)
                return;

            Object.keys(snapshot.val()).forEach(key => {
                dados.push(snapshot.val()[key]);
            });

              this.setState({
                postagens: dados
              });
           });

    }

   
    componentDidMount(){
        
        this.buscar();   
               
    }

    retornarDataAtual(dteString){
        let data = new Date();
        return data.getFullYear
    }

    publicar(e) {
        e.preventDefault();

        let userId = fire.auth().currentUser.uid;

        var postData = {
            texto: this.state.novapostagem,
            authorid: userId,
            count: 0};

        let newPostKey = fire.database().ref().child('postagens').push().key;
        var updates = {};
        updates['/postagens/' + newPostKey] = postData;
    
        fire.database().ref().update(updates)
            .then(function() {
               console.log("Registro inserido com sucesso.");

        })
        .catch(function(error) {
            console.log("Erro" + error.message);
        });
        
        this.buscar();   
               
    }

    logout() {
        fire.auth().signOut();
        window.location.href = '/'
    }

    handleChange(e) {
        this.setState({ novapostagem: e.target.value });
      }

    render() {
        return (
            <div className="container">
                <nav className="navbar navbar-light bg-dark">
                    <a className="navbar-brand" href="#"><b className="text-center">Gaitei</b></a>
                  

                    <a  onclick={this.logout}></a>
                </nav>

                <div className="row">
                    <div className="col-sm-12 col-md-12 col-lg-8">
                        <div className="box">
                            <form>
                                <textarea onChange={this.handleChange} rows="4" cols="50"> </textarea>
                                <button type="submit" onClick={this.publicar} className="btn">Publicar</button>
                            </form>
                        </div>
                    </div>

                     <div className="col-sm-12 col-md-12 col-lg-4">
                        <div className="box">
                            <div className="row">
                                <div className="col-xs-6">
                                    <img src="data:image/png;base64," alt="" className="img-circle">
                                    </img>
                                </div>
                                <div className="col-xs-6">
                                </div>
                            </div>
                        </div>    
                            
                    </div>
                </div>

                <div className="row">
                    <div className="col-sm-12 col-md-12 col-lg-8">
                        { this.state.postagens && this.state.postagens.map((c, i) => <div className="box" key={i}>{c.texto}</div>)}
                    </div>
                   
                </div>
            </div>
            
        );

    }

}

export default Home;