import React, { Component } from 'react';
import fire from '../config/Fire';
import loginIMG from'../images/login.png';
import SignUp from './SignUp';
import * as serviceAuth from '../serviceAuth';
import { withRouter } from 'react-router-dom'
import { Link } from 'react-router-dom'

import { toast } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';

import SimpleLineIcon from 'react-simple-line-icons';

class Login extends Component {
  constructor(props) {
    super(props);
    this.login = this.login.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.signup = this.signup.bind(this);
    this.state = {
      email: '',
      password: ''
    };
  }

  

  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  login(e) {
    e.preventDefault();
    serviceAuth.doSingIn(this.state.email,this.state.password);
  }

  signup(e){
    
    e.preventDefault();
    console.log('ENTROU');
    return <SignUp />;


    //fire.auth().createUserWithEmailAndPassword(this.state.email, this.state.password).then((u)=>{
    //}).then((u)=>{console.log(u)})
    //.catch((error) => {
     //   console.log(error);
     // })
  }
  render() {
    return (
      <div className="container-login">
        <div className="box-login p-l-55 p-r-55 p-t-80 p-b-30">
          <div className="form-login">
            <form>
              <div className="form-login-title"><b>(◕‿◕✿) </b></div>
              <br></br>

              <div className="form-login-title"><b>Gaitei.github.io</b></div>
              <div class="form-group">
                <label for="exampleInputEmail1">E-mail</label>
                <input value={this.state.email} onChange={this.handleChange} type="email" name="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" />
              </div>
              <div class="form-group">
                <label for="exampleInputPassword1">Senha</label>
                <input value={this.state.password} onChange={this.handleChange} type="password" name="password" class="form-control" id="exampleInputPassword1" placeholder="Password" />
              </div>
              <button type="submit" onClick={this.login} class="btn btn-green">Entrar</button>
              <button type="button" style={{marginLeft: '6px'}} className="btn btn-green">
                <Link to="/inscricao">Inscrever</Link>
              </button>
              <br></br>
              <small id="emailHelp" class="form-text text-muted">Seja bem vindo a Retardadice! Não ridicularize os grilos...</small>
           </form>
          </div>
      </div>      
      </div>
    );
  }
}
export default Login;