import React, { Component } from 'react';
import fire from '../config/Fire';
import 'bootstrap/dist/css/bootstrap.min.css';
import swal from 'sweetalert';

class SignUp extends Component {

    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.createAccount = this.createAccount.bind(this);
        this.state = {
            email: '',
            password: ''
          };
    }


    createAccount(e){ 
        e.preventDefault();
        fire.auth().createUserWithEmailAndPassword(this.state.email, this.state.password).then((u)=>{
            }).then((u)=>{swal({
                icon: "success",
              });})
        .catch((error) => {
            if (error.code === 'auth/email-already-in-use') {
                swal("Oops" , 'E-mail já em uso.',"error" );
            } else if (error.code === 'auth/invalid-email') {
                swal("Oops" , 'E-mail inválido',"error" );
            } else if (error.code === 'auth/user-disabled') {
                swal("Oops" , 'Usuário desabilitado',"error" );
            } else if (error.code === 'auth/operation-not-allowed') {
                swal("Oops" , 'Operação não permitida',"error" );
            } else if (error.code === 'auth/weak-password') {
                swal("Oops" , 'Senha é muito fraca',"error" );
            } else {
                swal(error.message);
            }
        })
    }     

    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    render() {
        return (
            <div className="container-login">
                <div className="box-login p-l-55 p-r-55 p-t-80 p-b-30">
                    <div className="form-login">
                    <form>
                        
                        <div className="form-login-title"><b>Crie sua conta</b></div>
                        <div class="form-group">
                            <label for="exampleInputName">Nome</label>
                            <input  type="name" name="name" class="form-control" id="InputName" placeholder="" />
                        </div>
                        <div class="form-group">
                            <label for="exampleInputEmail1">E-mail</label>
                            <input   value={this.state.email} onChange={this.handleChange} type="email" name="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="" />
                        </div>
                        <div class="form-group">
                            <label for="exampleInputPassword1">Senha</label>
                            <input    value={this.state.password} onChange={this.handleChange} type="password" name="password" class="form-control" id="exampleInputPassword1" placeholder="" />
                        </div>
                        <button  onClick={this.createAccount} className="btn btn-green">Criar conta</button>
                        <button style={{marginLeft: '6px'}} type="button" class="btn btn-red"  onClick={() => window.location.href = '/'} >Login</button>
                    </form>
                </div>
            </div>      
        </div>
            
        );

    }

}

export default SignUp;