import React, { Component } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
 
  class Message extends Component {

    constructor(props) {
        super(props);
    }

    notify = () => toast.error('teste', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        });
 
    render(){
      return (
        <div>
          <button onClick={this.notify}>this.props.texto</button>
          <ToastContainer />
        </div>
      );
    }
  }

  export default Message;